<template>
  <li @click="$router.push('/playlist?id=' + item.id)" class="card-item">
    <div class="pic">
      <img class="bg" :src="item.picUrl" />

      <span>
        <img
          style="width: 10px"
          src="data:image/svg+xml;base64,"
          alt=""
        />
        {{ item.playCount | Count }}</span
      >
    </div>

    <p>{{ item.name }}</p>
  </li>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
  },
  filters: {
    Count: function (value) {
      if (value > 100000000) {
        return (value / 100000000).toFixed(1) + "亿";
      } else if (value > 10000) {
        return (value / 10000).toFixed(1) + "万";
      } else {
        return value;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.card-item {
  flex-basis: 25%;
  flex-grow: 1;
  &:nth-child(3n + 2) {
    margin: 0 5px;
  }
  .pic {
    position: relative;
    img.bg {
      vertical-align: middle;
    }
    span {
      position: absolute;
      top: 0;
      right: 0;
      font-size: 10px;
      color: white;
    }
  }
  p {
    font-size: 13px;
    // padding: 50px 10px;
    margin: 5px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
</style>
