<template>
  <div class="home">
    <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
    <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
    
    <Title>编辑推荐</Title>
    <ul class="home-cardlist">
      <Playlistcard v-for="item in personalizeds" :key="item.id" :item="item"> </Playlistcard>
    </ul>
    <Title>最新音乐</Title>
    <ul class="home-songlist">
    <Musiclist v-for="item in Musiclist" :key="item.id" :item="item" 
    @change-current-song="$emit('change-current-song', $event);
    $emit('change-current-play-list', Musiclist)"
    :currentSongid="currentSongid"
    :playing="playing"
    >  
    </Musiclist>

    </ul>
  </div>
</template>

<script>
// @ is an alias to /src
import Title from "@/components/Title.vue";
import Playlistcard from "../components/Playlistcard.vue";
import Musiclist from "../components/Musiclist.vue";
export default {
  name: "Home",
  components: {
    Title,
    Playlistcard,
    Musiclist,
  },
  props:{
    currentSongid:{
      type:Number,
    },
    playing:Boolean,
  },
  data: function () {
    return {
      personalizeds: [],
      Musiclist:[],
    };
  },
  created: function () {
    this.axios
      .get("http://apis.netstart.cn/music/personalized?limit=6")
      .then((res) => {
        // console.log(res);
        this.personalizeds = res.data.result;
        // console.log(this.personalizeds);
      });
       this.axios
      .get("http://apis.netstart.cn/music/personalized/newsong")
      .then((res) => {
        // console.log(res);
        this.Musiclist = res.data.result;
        // console.log(this.Musiclist);
      });
  },
};
</script>
<style>
.home-cardlist {
  display: flex;
  flex-wrap: wrap;
}
</style>
