<template>
  <div class="search" @scroll="scroll">
    <input
      type="text"
      placeholder="搜索歌手"
      v-model="value"
      @keyup.enter="value && (searching = true)"
      @focus="inputing = true"
      @blur="inputing = false"
    />
    <section class="hot" v-if="!value && !searching">
      <h5>热门搜索</h5>
      <ul>
        <li
          v-for="hot in hots"
          :key="hot.first"
          @click="
            searching = true;
            value = hot.first;
          "
        >
          {{ hot.first }}
        </li>
      </ul>

      <ol>
          <li
          v-for="a in history"
          :key="a"
          @click="
            searching = true;
            value = a;
          "
        >
        {{ a }}</li>
      </ol>
    </section>
    <section class="suggests" v-if="value && !searching">
      <h5>搜索建议</h5>
      <ul>
        <li
          v-for="(item, index) in suggests"
          :key="index"
          @click="
            searching = true;
            value = item.keyword;
          "
        >
          {{ item.keyword }}
        </li>
      </ul>
   
    </section>
    <section class="suggests" v-if="searching">
      <h5>搜索结果</h5>
      <ul>
        <li v-for="(item, index) in searchiResults" :key="index">
          {{ item.name }}
        </li>
      </ul>
      <p v-if="!hasMore">已经没有了</p>
    </section>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      hots: [],
      value: "",
      suggests: [],
      searching: false,
      searchiResults: [],
      inputing: false,
      page: 0,
      hasMore: false,
      history: JSON.parse(window.localStorage.getItem("history")) || [],
    };
  },
  methods: {
    scroll: function (event) {
      if (this.hasMore) {
        if (
          event.target.offsetHeight + event.target.scrollTop ===
          event.target.scrollHeight
        ) {
          console.log("触底");
          this.axios
            .get("http://apis.netstart.cn/music/search", {
              params: {
                keywords: this.value,
                limit: 30,
                offset: this.page * 30,
              },
            })
            .then((res) => {
              this.searchiResults.push(...res.data.result.songs);
              this.page++;
              this.hasMore = res.data.result.hasMore;
            });
            this.history = [...new Set([...this.history,this.value])];
            window.localStorage.setItem("history", JSON.stringify(this.history));
        }
      } else {
          console.log("没有了");
      }
    },
  },
  created: function () {
    this.axios.get("http://apis.netstart.cn/music/search/hot").then((res) => {
      console.log(res);
      this.hots = res.data.result.hots;
    });
  },
  watch: {
    value: function (n) {
      if (this.inputing) {
        this.searching = false;
      }
      if (n) {
        this.axios
          .get("http://apis.netstart.cn/music/search/suggest", {
            params: {
              keywords: n,
              type: "mobile",
            },
          })
          .then((res) => {
            this.suggests = res.data.result.allMatch;
          });
      }
    },
    searching: function (n) {
      if (n && this.value) {
        this.axios
          .get("http://apis.netstart.cn/music/search", {
            params: {
              keywords: this.value,
              limit: 30,
              offset: this.page * 30,
            },
          })
          .then((res) => {
            this.searchiResults.push(...res.data.result.songs);
            this.page++;
            this.hasMore = res.data.result.hasMore;
          });
        this.history = [...new Set([...this.history,this.value])];
        window.localStorage.setItem("history", JSON.stringify(this.history));
      } else {
        this.searchiResults = [];
      }
    },
  },
};
</script>

<style lang="less" scoped>
.search {
  border: 20px;
  padding: 10px;
  .hot {
    ul {
        overflow: hidden;
      li {
        float: left;
        margin: 5px;
      }
    }
  }
}
</style>

