<template>
  <footer class="MusicBar" :class="{ playing: playing }">
    <section class="bar" @click="$emit(`toggle-show-play-page`,true)">
      <img
        :src="`${
          currentSong.song ? currentSong.picUrl : currentSong.al.picUrl
        }?imageView=1&type=webp&thumbnail=80x0`"
        alt=""
      />
      <h3>
        {{ currentSong.name }} -
        <span>{{
          currentSong.song
            ? currentSong.song.artists[0].name
            : currentSong.ar[0].name
        }}</span>
      </h3>

      <!-- <button @click="$emit(`toggic-playing-state`)">播放</button> -->
      <div class="progress" @click.stop="$emit(`toggic-playing-state`)">
        <canvas width="40" height="40" ref="canvas"></canvas>
        <div class="icon" :class="[playing ? `pause` : `play`]"></div>
      </div>
      <button @click.stop="$emit(`toggle-show-play-list`,true)">列表</button>
    </section>
  
  </footer>
</template>

<script>
// import Musiclist from "../components/Musiclist.vue";
export default {
  // components:{
  //   Musiclist,
  // },
  props: {
    currentSong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    currentPlayList:Array,
    
  },
 


  mounted: function () { 
    console.log(this.$refs.canvas);
  },
  computed: {
    percentage: function () {
      return this.currentTime / this.duration;
    },
  },
  watch: {
    percentage: function (n) {
      // console.log(n);
      var context = this.$refs.canvas.getContext("2d");
      context.clearRect(0, 0, 40, 40);

      context.beginPath();
      context.arc(
        20,
        20,
        17,
        (Math.PI / 180) * (0 - 90),
        (Math.PI / 180) * (360 * n - 90)
      );
      context.strokeStyle = "#d43c33";
      context.lineWidth = 1;
      context.stroke();

      context.beginPath();
      context.arc(
        20,
        20,
        18,
        (Math.PI / 180) * (360 * n - 90),
        (Math.PI / 180) * (360 - 90)
      );
      context.strokeStyle = "gray";
      context.lineWidth = 1;
      context.stroke();
    },
  },
};
</script>

