<template>
  <li v-if="item.song" class="songlist"
   @click="$emit('change-current-song', item)">
    <div class="left">
      <div class="title">
        {{ item.name }}
        <span v-for="n in item.song.alias" :key="n">{{n}}</span>
      </div>
      <div class="name">
        <em v-if="item.song.copyright === 0"></em>
        <i
          class="artist"
          v-for="artist in item.song.artists"
          :key="artist.id"
          >{{ artist.name }}</i
        >
        <b>{{ item.song.name }}</b>
      </div>
    </div>
    <div class="icon">
      <div class="play" :class="{current: currentSongid=== item.id, playing:playing}">
        <i></i>
        <i></i>
        <i></i>
      </div>
      
    </div>
  </li>

  <li v-else class="songlist" @click="$emit('change-current-song', item)">
     <div class="num"><slot></slot></div>
    <div class="left">
      <div class="title">
        {{ item.name }}
       <span v-for="n in item.alia" :key="n">{{n}}</span>
      </div>
      <div class="name">
        <!-- <em v-if="item.song.copyright === 0"></em> -->
        <i
          class="artist"
          v-for="artist in item.ar"
          :key="artist.id"
          >{{ artist.name }}</i
        >
        <b>{{ item.al.name }}</b>
      </div>
    </div>
    <div class="icon">

      <div class="play" :class="{current: currentSongid=== item.id,playing:playing}" >
        <i></i>
        <i></i>
        <i></i>
      </div>
      
    </div>
  </li>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
    currentSongid:{
      type:Number,
      
    },
    playing:Boolean,
  },
};
</script>

<style lang="less" scoped>
.songlist {
  padding: 5px 15px 5px 0px;
  margin-left: 15px;
  display: flex;
  // justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(201, 201, 201, 0.671);
   .num {
    padding: 0 12px 0 0px;
  }
  &.lt3 {
    .num {
      color: #d43c33;
    }
  }

  .left {
    flex: 1;
    .title {
      font-size: 17px;
      margin: 5px auto;
      margin-right: 10px;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      overflow: hidden;
      text-overflow: ellipsis;
      span{
        color: rgba(160, 159, 159, 0.699);
        
        &::before{
          content:"("
        }
        &::after{
          content:")"
        }
      }
    }
    .name {
      font-size: 14px;
      color: #888;
       display: -webkit-box;
       -webkit-box-orient: vertical;
       -webkit-line-clamp: 1;
       overflow: hidden;
       text-overflow: ellipsis;
       em {
        display: inline-block;
        width: 12px;
        height: 8px;
        margin-right: 4px;
        background-image: url(https://s3.music.126.net/mobile-new/img/index_icon_2x.png);
        background-repeat: no-repeat;
        background-size: 166px;
      }
      i.artist {
        font-style: normal;
        &:after {
          content: "/";
          margin: 0 3px;
        }
        &:last-of-type {
          //    background-color: red;
          &::after {
            content: "-";
            margin: 0 5px;
          }
        }
      }
      b {
        font-weight: normal;
      }
    }
  }
  .icon {
    width: 22px;
    height: 22px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    .play{
    // position: absolute;
    // top:0;
    // left: 0;
    width: 100%;
    height: 100%;
    background-image: url(https://s3.music.126.net/mobile-new/img/index_icon_2x.png);
    background-repeat: no-repeat;
    background-position: -24px 0;
    background-size: 166px auto;
    
    }
    .current{
    width: 20px;
    height: 20px;
    // background-color: red;
    background: none;
    display: flex;
    justify-content: space-around;
     i{
       width: 3px;
       height: 100%;
       background-color: #d43c33;
      //  display: inline-block;
      transform-origin: bottom;
      animation: playing 0.9s linear 0s infinite alternate ;
      animation-play-state: paused;
      &:nth-child(1){
        animation-delay: -0.6s;
      }
      &:nth-child(2){
        animation-delay: -0.3s;
      }

     }
     &.playing{
       i{
         animation-play-state: running;
       }
     }
    }
  }
  @keyframes playing {
    from{
    //  height: 20%;
    transform: scaleY(0.2);
    }
    to{
      transform: scaleY(1);
    }
  }
}
</style>