<template>
  <section class="play-page">
    <div
      class="mask"
      :style="{
        backgroundImage: `url(${
          currentSong.song ? currentSong.picUrl : currentSong.al.picUrl
        }?imageView=1&type=webp&thumbnail=246x0)`,
      }"
    ></div>
    <button @click="$emit('toggle-show-play-page', false)">paly-page</button>
    <div class="rotate">
      <img
        class="needle"
        :class="{paused:!playing}"
        src="https://s3.music.126.net/mobile-new/img/needle-ab.png"
        alt=""
      />
      <section class="record" :class="{ playing: playing }">
        <img
          class="pic"
          :src="currentSong.song ? currentSong.picUrl : currentSong.al.picUrl"
          alt=""
        />
        <img
          class="disc"
          src="https://s3.music.126.net/mobile-new/img/disc.png"
          alt=""
        />
      </section>
      
    </div>
    <section class="progress">
      <input type="range" :max="duration" step="0.5" :value="currentTime"
       @change="progressChange"
       @input="inputing=true"
       >
    </section>
    <section class="moshi">
      <span @click="$emit(`change-model`)">模式</span>
      <span @click="$emit(`prev-song`)">上</span>
      <span @click="$emit(`toggic-playing-state`)">播</span>
      <span @click="$emit(`next-song`)">下</span>
      <span @click.stop="$emit(`toggle-show-play-list`,true)">列表</span>
    </section>
  </section>
</template>

<script>
export default {
  props: {
    currentSong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
  },
  data:function () {
    return{
      value:this.currentTime,
      inputing:true,
    };
  },
  watch:{
    currentTime:function (n) {
      if(!this.inputing){

        this.value=n;
      } 
    }
  },
  methods:{
    progressChange:function (event) {
      this.inputing = false;
      console.log(event.target.value);
      this.$emit("current-time-change",event.target.value);
    }
  }
  // computed:{
    
  // }
};
</script>

<style lang="less" scoped>
.play-page {
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  &::before{
      content: "";
      display: block;
       position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -2;
      background-color: black;

    }

  .mask {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: absolute;
    filter: blur(30px);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    transform: scale(1.5);
    transition: all 0.3s;
   
  }
  .rotate {
    position: relative;
    padding-top: 20vw;
    .needle{
      height: 40vw;
      position: absolute;
      left: 50%;
      top: 0;
      margin-left: -15px;
      z-index: 5;
      transform-origin: 10px 10px;
      transform: rotate(357deg);
      transition: all 0.3s;
      &.paused{
        transform: rotate(339deg);
      }
    }
    .record {
      position: relative;
      width: 80vw;
      height: 80vw;
      margin: 0 auto 0 auto;
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        &.pic {
          transform: scale(0.8);
        }
      }
      animation: rotate 8s linear infinite;
      animation-play-state: paused;
      &.playing {
        animation-play-state: running;
      }
    }
  }
  .moshi{
    padding: 20%;
    display: flex;
    justify-content: space-around;
    color: white;
  }
  .progress{
    width: 80vw;
    margin: 20px auto;
    input
    {
      width: 100%;
    }
  }
}
</style>