<template>
  <div v-if="detail">
    PlayList
    <ul>
      <!-- <li v-for="item in detail.tracks" :key="item.id">{{ item.name }}</li> -->

      <Musiclist
        v-for="(item,index) in detail.tracks"
        :key="item.id"
        :item="item"
        @change-current-song="
        $emit('change-current-song', item);
        $emit('change-current-play-list', detail.tracks) "
        :currentSongid="currentSongid"
        :playing="playing"
        :class="{ lt3:index<3}"
      >
      {{index+1}}</Musiclist>
    </ul>
  </div>
</template>

<script>
import Musiclist from "@/components/Musiclist.vue";
export default {
  components: {
    Musiclist,
  },

  data: function () {
    return {
      detail: null,
    };
  },
  props: {
    currentSongid: {
      type: Number,
    },
    playing: Boolean,
  },
  methods: {
    getPlayListDetail: function (id) {
      this.axios
        .get("http://apis.netstart.cn/music/playlist/detail", {
          params: {
            id,
          },
        })
        .then((res) => {
          //   console.log(res);
          this.detail = res.data.playlist;
        });
    },
  },
  created: function () {
    this.getPlayListDetail(this.$route.query.id);
  },
};
</script>

