  li {
    text-align: center;
    flex: 1;
    // background-color: pink;
    a {
      line-height: 2;
      display: inline-block;
      text-decoration: none;
      padding: 0 5px;
      color: #2c3e50;
      font-size: 15px;
      &.router-link-exact-active {
        color: #d43c33;
        border-bottom: 2px solid #d43c33;
      }
    }
  }
}
